@keyframes loaderSpinner {
0% {
    transform: rotate(0deg);
}
100% {
    transform: rotate(360deg);
}
}
.loader-overlay {
  position: fixed;
  top: 0;
  animation: fade 4s linear;
  z-index: 99999;
  background-color: #fff;
  width: 100vw;
  height: 100vh;
}
.loader {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateZ(0);
  animation: loaderSpinner 1.1s infinite linear;
  z-index: 999999;
  margin: -20px 0 0 -20px;
  border-top: 5px solid #bfccca;
  border-right: 5px solid #bfccca;
  border-bottom: 5px solid #bfccca;
  border-left: 5px solid #607874;
  text-indent: -9999em;
  font-size: 10px;
  -webkit-transform: translateZ(0);
  -ms-transform: translateZ(0);
  -webkit-animation: loaderSpinner 1.1s infinite linear;
}
.loader, .loader:after {
    border-radius: 50%;
    width: 40px;
    height: 40px;
}
