.link2 {
  height: 38px;
  line-height: 38px;
  display: inline-block;
  text-align: center;
  color: #fff;
  text-decoration: none;
  font-weight: 600;
  border-radius: 40px;
  font-size: 14px;
  background: #be1625;
  padding: 0px 20px;
