}
}

/* Main class for all */
#header-outer {
  width: 100%;
  top: 0px;
  left: 0px;
  position: relative;
  overflow: visible !important;
  padding: 28px 0px 0px 0px;
  padding-top: 28px;
  background-color: #fff;
  z-index: 9;
  box-shadow: 0 0 3px 0 rgba(0, 0, 0, 0.33);
  -moz-box-shadow: 0 0 3px 0 rgba(0, 0, 0, 0.33);
  -webkit-box-shadow: 0 0 3px 0 rgba(0, 0, 0, 0.33);
  float: left;
}
.headerphone {
  font-size: 16px;
  color: #01467F;
  text-align: right;
  line-height: 21px;
}
header#top {
  position: relative;
  z-index: 9998;
  width: 100%;
}
header#top img {
    height: 80px !important;
    margin-top: -8px !important;
}
#footer-outer {
  color: #ccc;
  position: relative;
  background-color: #2d3032;
  width: 100%;
  float: left;
  position: relative;
  padding: 55px 0;
}
#footer-outer p {
    font-size: 12px;
    color: #ccc;
}
#footer-outer h4 {
    color: #777;
    font-size: 12px;
    letter-spacing: 1.5px;
    text-transform: uppercase;
    margin-bottom: 20px;
    font-weight: bold;
}
#footer-outer .col3 {
    padding-right: 2%;
}
#copyright {
  padding: 20px 0px;
  font-size: 12px;
  background-color: #1f1f1f;
  border-top: 1px solid #3a3a3a;
  color: #777;
  clear: both;
}
#copyright p {
    margin-top: 3px;
}
#copyright a {
    color: #ccc;
    text-decoration: none;
}
.our-orthodontic-section {
  padding-top: 90px !important;
  padding-bottom: 90px !important;
  background-position: center;
  background-repeat: no-repeat !important;
  background-size: cover !important;
  text-align: center;
  width: 100%;
  float: left;
}
.our-orthodontic-section .col4 .col4-box {
    background-color: #fff;
    margin: 5px;
    padding: 20px 15px;
}
.our-orthodontic-section h5 {
    color: #000;
    font-size: 15px;
    padding-bottom: 28px;
}
.our-orthodontic-section p {
    padding-bottom: 27px;
}
.read-more-button {
  border: 1px solid #2A2A2A;
  background-color: transparent;
  padding: 13px 19px;
  font-size: 14px;
  text-transform: uppercase;
  cursor: pointer;
  position: relative;
  top: 0;
  transition: all .2s ease-in-out;
