}

/* ---- desktop menu ---- */
.navigation-main {
  margin: 0;
  height: auto;
}
#nav > a {
  display: none;
}
.navigation-main a {
  text-decoration: none;
}
.togl {
  float: right;
}
.navigation-main a {
  text-decoration: none;
}
.navigation-main a.cross {
    color: #fff;
    font-size: 25px;
    font-weight: 700;
}
#nav {
  float: right;
}
#nav > ul {
    height: auto;
    text-align: center;
    display: block;
}
.navigation-main ul {
  list-style: none;
  margin: 0;
  padding-left: 0;
}
#nav li {
  position: relative;
}
.navigation-main li {
  color: #ffffff;
  display: inline-block;
  float: none;
  padding: 0px;
  position: relative;
  text-decoration: none;
  transition-duration: 0.5s;
}
.navigation-main ul li a {
  color: #888;
  font-size: 13px;
  font-weight: normal;
  line-height: 12px;
  padding: 33px 16px 62px 12px;
  font-size: 13px;
  display: block;
  transition: color 0.1s linear;
}
.navigation-main ul li a:hover {
    color: #00467e !important;
}
#nav li ul {
  display: none;
  position: absolute;
  top: 100%;
}
.navigation-main ul li ul.dropdown {
  margin-top: 0;
  top: 54px;
  width: auto;
  z-index: 9;
  background-color: #2b3032;
  width: 180px;
}
.navigation-main ul li ul.dropdown li {
    float: left;
    margin: 0;
    padding: 0;
    position: relative;
    width: 100%;
}
.navigation-main ul li ul.dropdown li a {
      color: #ccc;
      font-size: 12px;
      margin: 0;
      padding: 8px 16px;
      text-transform: none;
      display: block;
      text-align: left;
      line-height: 14px;
      border-right: 0;
      background-color: #1F1F1F;
      padding: 14px;
      border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}
.navigation-main ul li ul.dropdown li a:hover {
        color: #629cbe;
        background-color: #2D3032;
        color: #ddd !important;
}
#nav li:hover ul {
  display: block;
}
.navigation-main li:hover {
  cursor: pointer;
}
.navigation-main ul li:hover > ul {
  visibility: visible;
  opacity: 1;
  display: block;
}
.navigation-main ul li ul:hover {
  visibility: visible;
  opacity: 1;
  display: block;
}
.navigation-main ul li ul.dropdown li:hover {
  background: none;
}
.navigation-main ul li ul.dropdown li a:hover {
  color: #629cbe;
}
#nav li:hover ul li:hover {
  display: block;
}
#nav li:hover ul li ul {
  display: none;
  position: absolute;
  top: 100%;
}
#nav li:hover ul li:hover ul, #nav li:hover ul li ul li:hover {
  display: block;
}
.navigation-main ul li ul.dropdown li ul.dropdown {
  margin-top: 0;
  left: 98%;
  width: auto;
  z-index: 9;
  background-color: #2b3032;
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
  margin-top: -23px;
}

